<template>
  <v-container>
    <v-row dense>
      <v-col
        v-for="(item, i) in desserts"
        :key="i"
        cols="12"
      >
        <v-card
          class="mx-auto"
          max-width="344"
          outlined
        >
          <v-list-item three-line>
            <v-list-item-content>
              <div class="overline mb-4">MAKANAN</div>
              <v-list-item-title class="headline mb-1" v-text="item.name" label="Dessert name" ></v-list-item-title>
              <v-list-item-subtitle v-model="editedItem.calories" label="Calories">Calories</v-list-item-subtitle>
              <v-list-item-subtitle>Fat</v-list-item-subtitle>
              <v-list-item-subtitle>Carbs</v-list-item-subtitle>
              <v-list-item-subtitle>Protein</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>

          <v-card-actions>
            <v-icon class="mr-1" @click="editItem(item)">Edit</v-icon>
            <v-icon class="mr-1" @click="deleteItem(item)">Delete</v-icon>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

import {
  dessertsRef
} from '../firebase';

export default {
  data: () => ({
    cek: -1,
    search: '',
    dialog: false,
    selected: [],
    marked: -1,
    headers: [{
        text: 'Dessert (100g serving)',
        align: 'left',
        sortable: false,
        value: 'name',
      },
      {
        text: 'Calories',
        value: 'calories'
      },
      {
        text: 'Fat (g)',
        value: 'fat'
      },
      {
        text: 'Carbs (g)',
        value: 'carbs'
      },
      {
        text: 'Protein (g)',
        value: 'protein'
      },
      {
        text: 'Actions',
        value: 'action',
        sortable: false
        }
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      name: '',
      calories: 0,
      fat: 0,
      carbs: 0,
      protein: 0
    },
    defaultItem: {
      name: '',
      calories: 0,
      fat: 0,
      carbs: 0,
      protein: 0
    }
  }),
  firebase: {
    desserts: dessertsRef
  },
  computed: {
    formTitle () {
      return this.cek === -1 ? 'New Item' : 'Edit Item'
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    }
  },
  methods: {
    close () {
      this.dialog = false
    },
    // methods untuk edit
    editItem (item) {
      this.editedIndex = item['.key']
      this.cek = 0
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      confirm('Are you sure you want to delete this item?') && dessertsRef.child((item['.key'])).remove()
    },
    // methods untuk save
    save () {
      if (this.cek > -1) {
        dessertsRef.child(this.editedIndex).set({
          name: this.editedItem.name,
          calories: this.editedItem.calories,
          fat: this.editedItem.fat,
          carbs: this.editedItem.carbs,
          protein: this.editedItem.protein
        })
        this.clear()
      } else {
        dessertsRef.push({
          name: this.editedItem.name,
          calories: this.editedItem.calories,
          fat: this.editedItem.fat,
          carbs: this.editedItem.carbs,
          protein: this.editedItem.protein
        })
        this.clear()
      }
      this.close()
    },
    clear () {
      this.editedItem = {}
      this.editedItem.name = ''
      this.editedItem.calories = ''
      this.editedItem.fat = 0
      this.editedItem.carbs = 0
      this.editedItem.protein = 0
      this.cek = -1
    }
  }
}
</script>
